<template>
    <v-container class="send-change-password-email" style="position: relative">
        <v-row>
            <v-col cols="12">
                <h2>{{forgotPasswordText}}</h2>
            </v-col>
        </v-row>

        <v-col cols="12">
            <v-card>
                <v-progress-linear
                        v-model="progressValue"
                        height="10"
                        color="blue-grey"
                        reactive
                        dark
                ></v-progress-linear>

                <v-container>
                    <v-card-text class="pa-0" align="justify">
                        {{introduceEmailText}}
                    </v-card-text>

                    <v-form v-model="validForm">
                        <v-text-field
                                v-model="email"
                                :rules="[rules.required, rules.email]"
                                label="E-mail"
                                required
                        ></v-text-field>
                    </v-form>

                    <v-btn block color="primary" @click="sendChangePasswordEmail" :disabled="buttonIsDisabled">
                        {{resetPasswordText}}
                    </v-btn>
                </v-container>
            </v-card>
        </v-col>

        <v-dialog
                v-model="dialog"
                max-width="500px"
        >
            <v-card>
                <br>
                <v-card-text align="justify" style="color: black; padding-bottom: 0px;">
                    <p style="margin-bottom: 7px">{{resetPasswordInstructionsText}}</p>
                </v-card-text>
                <v-card-actions style="padding-bottom: 15px">
                    <v-spacer></v-spacer>
                    <v-btn @click="redirectToHome" color="primary">
                        {{textContinue}}
                    </v-btn>
                    <v-spacer></v-spacer>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapActions} from "vuex";

    // Página de petición de reseteo de contraseña
    // @group Cuenta
    export default {
        name: "SendChangePasswordEmail",
        data() {
            return {
                progressValue: 100,
                validForm: false,
                email: '',
                buttonIsDisabled: true,
                dialog: false,
                snackbar: false,
                snackbarText: '',
                rules: {
                    required: value => !!value || this.$i18n.t('messages.RULE_TEXT_MANDATORY_FIELD'),
                    email: value => {
                        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                        return pattern.test(value) || this.$i18n.t('messages.TEXT_EMAIL_NOT_VALID')
                    }
                },
                closeText: null,
                forgotPasswordText: null,
                introduceEmailText: null,
                resetPasswordText: null,
                resetPasswordInstructionsText: null,
                textContinue: null
            }
        },
        methods: {
            ...mapActions('user', ['sendPasswordResetEmail']),

            // @vuese
            // Envía un correo de reseteo de contraseña al E-mail introducido por el usuario
            sendChangePasswordEmail: function () {
                let self = this;

                let resultSendPasswordResetEmail = this.sendPasswordResetEmail(self.email);

                resultSendPasswordResetEmail
                    .then(() => {
                        self.dialog = true;
                    })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },

            // @vuese
            // Redirige al usuario a la página de inicio de la aplicación
            redirectToHome: function () {
                this.dialog = false;
                this.$router.replace('home');
            }
        },
        created() {
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.forgotPasswordText = this.$i18n.t('messages.TEXT_HAVE_YOU_FORGOTTEN_YOUR_PASSWORD');
            this.introduceEmailText = this.$i18n.t('messages.TEXT_ENTER_YOUR_EMAIL_TO_RESET_YOUR_PASSWORD');
            this.resetPasswordText = this.$i18n.t('messages.TEXT_RESET_PASSWORD');
            this.resetPasswordInstructionsText = this.$i18n.t('messages.TEXT_RESET_PASSWORD_INSTRUCTIONS');
            this.textContinue = this.$i18n.t('messages.TEXT_TO_CONTINUE');
        },
        watch: {
            validForm: function (value) {
                if (value) {
                    this.buttonIsDisabled = false
                } else {
                    this.buttonIsDisabled = true
                }
            }
        }
    }
</script>

